<template>
	<div class="circleIndex">
		<!--  头部  -->
		<div class="circleIndexHead">
			<div class="circleIndexHeadCon">
				<span class="circleCharacter">
					圈子
				</span>
				<img @click="more" class="circleImg" src="../../assets/images/23/circle_to_down_arrow.png" alt="" />
				<div class="more" v-show="none.flag">
					<ul>
						<li class="frist">全部</li>
						<li>热门</li>
						<li>关注</li>
					</ul>
				</div>
				<div class="circleIndexIcon">
					<router-link @click="demo(data.id)" :to="{path:'/circle/circleHair'}"><img src="../../assets/images/23/circle_edit_icon_normal.png" alt="" /></router-link>
				</div>
			</div>
		</div>
		<!--  内容区  -->
		<div class="box" v-for="(data,index) in list">
			<div class="circleContent">
				<div class="circleContentCar">
					<div class="circlePortrait">
						<router-link @click="demo(data.id)" :to="{path:'/circle/circleMaster'}"><img src="../../assets/images/23/head1.png" alt="" /></router-link>
					</div>
					<div class="circleAuthor">
						{{data.circleAuthor}}
					</div>
					<div class="circleLike">
						<img src="../../assets/images/23/z-1.png" alt="" />
					</div>
					<div class="circleQuantity">
						{{data.circleQuantity}}
					</div>
					<div class="circleTime">
						{{data.circleTime}}
					</div>
					<div class="circleSite">
						{{data.circleSite}}
					</div>
					<div class="circleContenter">
						<router-link @click="demo(data.id)" :to="{path:'/circle/circleDetails'}">{{data.circleContenter}}</router-link>
					</div>	
					<div class="Img">
						<img :src="data.Url" alt="" />
					</div>
				</div>
			</div>
			<div class="circleMes" @click="mes(index)">
				<img src="../../assets/images/23/01.png" alt="" />
			</div>
			<div class="circleComment" v-show="none.flagMes">
				<div class="circleTread">
					<img src="../../assets/images/23/t.png" alt="" />
					<span>踩(25)</span>
				</div>
				<div class="circleShare">
					<img src="../../assets/images/23/t1.png" alt="" />
					<span>分享</span>
				</div>
				<div class="circleCom">
					<img src="../../assets/images/23/t2.png" alt="" />
					<span>评论</span>
				</div>
			</div>

		</div>
	</div>
</template>

<script>
	export default {
		name: "circle",
		created() {
			this.$axios.get("/circle", {
			}).then(res => {
				this.list = res.data;
			})
		},
		data() {
			return {
				none: {
					// 导航是否显示
					flag: false,
					// 评论信息是否显示
					flagMes: false
				},
				//
				list: [{
						circleAuthor: "Burke",
						id: 0,
						circleQuantity: 1500,
						circleTime: "刚刚",
						circleSite: "北京",
						circleContenter: "译文太阳出来就去耕作田地，太阳落山就回家去休息。凿一眼井就可以有水喝，种出庄稼就不会饿肚皮。这样的日子有何不自在，谁还去羡慕帝王的权力。注释壤：据论是古代儿童玩具，以木做成，前宽后窄，长一尺多，形如鞋。玩时，先将一壤置于地，然后在三四十步远处，以另一壤击之，中者为胜。",
						Url:""
					}
				]
			}
		},
		methods: {
			more() {
				var flag = !this.none.flag;
				this.none.flag = flag;
			},
			mes(index) {			
				
				this.none.flagMes = !this.none.flagMes;

			},
			demo(id) {
				console.log(id);
			}
		}
		

	}
</script>

<style lang="less">
	*{
		margin: 0;padding: 0;
	}
	.circleIndex {
		margin-bottom: 110/64rem;
		.circleIndexHead {
			background: #a54be8;
			width: 100%;
			height: 90/64rem;
			.circleIndexHeadCon {
				position: relative;
				text-align: center;
				.circleCharacter {
					font-size: 36/64rem;
					line-height: 90/64rem;
					color: #fff;
				}
				.more {
					position: absolute;
					z-index: 100;
					width: 100%;
					height: 100/75rem;
					background: rgba(165,75,232,0.7);
					ul {
						overflow: hidden;
						li {
							font-size: 30/75rem;
							display: inline-block;
							margin-top: 20/75rem;
							line-height: 60/75rem;
							width: 130/75rem;
							height: 60/75rem;
							color: #fff;
							border-radius: 50/75rem;
						}
						.frist {
							background: rgba(145,69,211,0.7);
						}
					}
				}
				.circleImg {
					width: 27/75rem;
					height: 27/75rem;
				}
				img {
					width: 36/75rem;
					height: 37/75rem;
				}
				.circleIndexIcon {
					position: absolute;
					bottom: 36%;
					right: 30/75rem;
					/*margin-left: 100/75rem;*/
				}
			}
		}
		.box {
			margin-top: 28/64rem;
			position: relative;
			div {
				display: inline-block;
			}
			.circleMes {
				/*position: absolute;*/
				margin: -10/75rem 0 0 650/75rem;
				right: 20/75rem;
				bottom: 28/75rem;
				img {
					width: 60/75rem;
					height: 40/75rem;
				}
			}
			.circleComment {
				position: absolute;
				bottom: 0/75rem;
				right: 100/75rem;
				/*display: inline-block;*/
				/*margin: -100/75rem 0 0 150/75rem;*/
				/*margin-top: -1100/75rem;*/
				background: #493d52;
				height: 70/75rem;
				border-radius: 5/75rem;
				.circleTread,
				.circleShare,
				.circleCom {
					height: 50/75rem;
					margin: 10/75rem;
					background: #6c597a;
					border-radius: 5/75rem;
				}
				img {
					width: 30/75rem;
					height: 30/75rem;
					vertical-align: middle;
					margin-top: -15/75rem;
					margin-left: 12/75rem;
				}
				span {
					margin: 0 10/75rem;
					font-size: 30/75rem;
					line-height: 50/75rem;
					color: #fff;
				}
			}
			.circleContent {
				/*height: 470/75rem;*/
				margin: 0 0 40/75rem 20/64rem;
				.circleContentCar {
					z-index: 0;
					overflow: hidden;
					position: relative;
					div {
						float: left;
					}
					.circlePortrait img {
						width: 70/75rem;
						height: 70/75rem;
					}
					.circleAuthor {
						font-size: 30/64rem;
						color: #c79bef;
						margin-left: 15/75rem;
					}
					.circleLike img {
						width: 50/75rem;
						height: 50/75rem;
						margin: 5/75rem 0 0 410/75rem;
					}
					.circleQuantity {
						font-size: 25/64rem;
						color: #959595;
						line-height: 60/75rem;
						margin-left: 8/75rem;
					}
					.circleTime,
					.circleSite {
						position: absolute;
						top: 35/75rem;
						font-size: 35/75rem;
						color: #959595;
					}
					.circleTime {
						left: 85/75rem;
					}
					.circleSite {
						left: 180/75rem;
					}
					.circleContenter {
						width: 600/75rem;
						margin-top: 20/75rem;
						font-size: 35/75rem;
						margin-left: 85/75rem;
					}
					.Img{
						margin-top: 20/75rem;
						margin-left: 85/75rem;
					}
				}
			}
		}
	}
</style>


