<template>
	<div class="circleMaster">
		<div class="circleMasterHead">
			<router-link :to="{path:'/circle'}">
				<img src="../../../assets/images/24/f-1.png" alt="" />
			</router-link>
			<span>大咖的片花</span>
		</div>
		<div class="circleMasterCon" v-for="data in list">
			<div class="circleMasterImg">
				<img :src="data.src" alt="" />
			</div>
			<div class="circleMasterTen">
				<h1>{{data.title}}</h1>
				<div class="circleMasterCharacter">
					<span>{{data.content}}</span>
				</div>
			</div>
		</div>
		<div class="footer">
			<div class="circleMasterBox">
				<div class="circleMasterIcon">
					<img src="../../../assets/images/27/上拉.png" alt="" />
				</div>
				<div class="circleMasterWz">
					上拉加载更多
				</div>
			</div>

		</div>
	</div>
</template>

<script>
	import png1 from '../../../assets/images/27/01.png'
	import png2 from '../../../assets/images/27/02.png'
	import png3 from '../../../assets/images/27/03.png'
	import png4 from '../../../assets/images/27/04.png'
	export default {
		name: "circleMaster",
		data() {
			return {
				list: [{
						title: "第一集",
						content: "简介：失恋男姜义哲，不慎误入游乐园魔法世界，在这段奇妙的旅程中与公主安洁西之间产生了纯美...",
//						src: "/static/img/01.e1e24f2.png"
						src: png1
					},
					{
						title: "第二集",
						content: "简介：失恋男姜义哲，不慎误入游乐园魔法世界，在这段奇妙的旅程中与公主安洁西之间产生了纯美...",
						src: png2
					},
					{
						title: "第三集",
						content: "简介：失恋男姜义哲，不慎误入游乐园魔法世界，在这段奇妙的旅程中与公主安洁西之间产生了纯美...",
						src: png3
					},
					{
						title: "第四集",
						content: "简介：失恋男姜义哲，不慎误入游乐园魔法世界，在这段奇妙的旅程中与公主安洁西之间产生了纯美...",
						src: png4
					}
				]
			}
		}
	}
</script>

<style lang="less">
	.circleMaster {
		.circleMasterHead {
			width: 100%;
			height: 90/64rem;
			text-align: center;
			line-height: 90/64rem;
			position: relative;
			background: #a54be8;
			img {
				left: 40/64rem;
				top: 30/64rem;
				position: absolute;
				width: 20/64rem;
				height: 35/64rem;
			}
			span {
				font-size: 36/64rem;
				color: #fff;
			}
		}
		.circleMasterCon {
			margin: 20/64rem 0 10/64rem 20/64rem;
			overflow: hidden;
			.circleMasterCon:before {
				position: absolute;
				content: "";
				width: 80%;
				height: 10/64rem;
				background: #d7d2e0;
				bottom: -20/64rem;
			}
			.circleMasterImg,
			.circleMasterTen {
				float: left;
			}
			.circleMasterImg {
				img {
					height: 150/64rem;
				}
			}
			.circleMasterTen {
				width: 360/64rem;
				position: relative;
				h1 {
					font-size: 34/64rem;
					font-weight: bold;
					margin: 0 0 10/64rem 20/64rem;
				}
				.circleMasterCharacter {
					margin: 0 0 0 20/64rem;
					span {
						font-size: 28/64rem;
						color: #959595;
					}
				}
			}
			.circleMasterWire {
				width: 80%;
				height: 1/64rem;
				/*background: #d7d2e0;*/
				background: red;
				/*margin: 50/64rem 0;*/
			}
		}
		.footer {
			.circleMasterBox {
				width: 150/64rem;
				margin: 0 auto;
				margin-top: 230/64rem;
				.circleMasterIcon,.circleMasterWz {
					display: inline-block;
				}
				.circleMasterWz{
					font-size: 16/64rem;
					margin-left: 14/64rem;
				}
				.circleMasterIcon {
					img {
						vertical-align: middle;
					}
				}
			}
		}
	}
</style>


