<template>
    <div class="jdIndex_footer">
        <div class="footerContainer">        	
			<ul class="clearfix">
 
                    <router-link :to="{path:'/'+btn.id}" v-for="(btn,index) in btnList" active-class='active' tag="li">	
	                    <div class='dIndex_footer_nav'>
	                    	<div><img :src="btn.url1"  alt="" /></div>
	                    	<div class="title">{{btn.title}}</div>
	                    </div>    
                    </router-link>
                    
          </ul>           
		</div>
    </div>
</template>
<script>
	import img1 from "../assets/images/1/main_trailer_selected.png"
	import img2 from "../assets/images/1/main_circle_selected.png"
	import img3 from "../assets/images/1/main_dynamic_selected.png"
	import img4 from "../assets/images/1/main_profile_selected.png"
    export default{
        name:"footer",
        data() {
			return {
				btnList:[
				
				{
					title:"我的",
					id:"mine",
					url1:img4
				},
				{
					title:"动态",
					id:"change",
					url1:img3
				},
				{
					title:"圈子",
					id:"circle",
					url1:img2
				},
				{
					title:"片花",
					id:"flower",
					url1:img1
					
				}
				]
			}
		},
		methods:{
			jdIndex_footer_gl:function(event){
				console.log(event);
				var jdgl=document.getElementById('dIndex_footer_nav');
				


				
			}
			
		}
    }
    
</script>

<style lang="less">
	.jdIndex_footer{
		position: fixed;
		bottom: 0;
	}

	.jdIndex_footer ul{
		height: 100/75rem;
		background-color: rgb(243,243,243);
		opacity:0.9;
		filter:alpha(opacity=40); /* 针对 IE8 以及更早的版本 */
	}
	.jdIndex_footer ul li{
		width: 185/75rem;
		/*border: 1px solid saddlebrown;*/
		float: right;
		opacity:0.2;
		filter:alpha(opacity=20);  
		margin-top: 18/75rem;
		
	}
	.dIndex_footer_nav div{
		margin-left: 66/75rem;

		font-size: 22/75rem;

	}
	
	
	.jdIndex_footer{
		color: rgb(213,192,224);
	}
	.jdIndex_footer .active{
		opacity:1;
        filter:alpha(opacity=1); 
        color: rgb(165,75,232);
		
	}
	
	.dIndex_footer_nav img{
		height:40/75rem;
		width:40/75rem;
        margin-bottom: 7/75rem;
	}
	
	
	/*.jdIndex_footer .active .dIndex_footer_nav div {
		color: red;
	}*/

	
	

</style>



