<template>
	<div class="mineSignature">
		<MineSetBtnHead :getTitle="title"/>
		<div class="signContent">
			<textarea class="content" resize="none" name="" rows="" cols=""></textarea>
			<span>30字</span>
		</div>
	</div>
</template>

<script>
	import MineSetBtnHead from '../../../comments/mineSetBtnHead';
	export default{
		name:'mineSignature',
		data(){
			return{
				title:{
					names:'个性签名',
					method:'保存',
					paths:'minePersonalData'
				}
			}
		},
		components:{
			MineSetBtnHead
		}
	}
</script>

<style lang="less">
	.mineSignature{
		.signContent{
			margin-top:30/64rem;
			position:relative;
			.content{
				width:10-62/64rem;
				height:150/64rem;
				font-size: 28/64rem;
				color:#B5B0BF;
				padding:10/64rem 30/64rem;
				border:none;
				border-bottom: 1/64rem solid #d7d2e0;
				border-top: 1/64rem solid #d7d2e0;
				overflow-x: hidden;
				overflow-y: hidden;
				font-family: 'simsun';
				outline: none;
			}
			span{
				position:absolute;
				bottom:20/64rem;
				right:30/64rem;
				font-size: 28/64rem;
				color:#B5B0BF;
				font-family: 'simsun';
				
			}
		}
	}
</style>


