<template>
	<div class="mineStarAuten">
		<div class="starAutenContainer">			
			<MineHead :getTitle="title"/>
			<div class="starMiddle">
				
				<div class="starInput">
					<div class="starInput1">
						<input type="text" v-focus="['请输入你的真实姓名','']" placeholder="请输入你的真实姓名" />
						<span>必填</span>
					</div>
					<div class="starInput2">
						<input type="text" v-focus="['请输入联系方式','']" placeholder="请输入联系方式" />
						<span>必填</span>
					</div>	
				</div>
				
				<div class="starContent">
					<p class="title">请上传如左图所示的图片</p>
					<div class="srcContent clearfix">
						<div class="srcLeft">
							<img src="../../../assets/images/49-50/示例照片.png"/>
						</div>
						<div class="srcRight">
							<img src="../../../assets/images/49-50/s-1.png"/>
							<p class="upload">上传照片必填</p>
						</div>						
					</div>
				</div>
				<p class="starTitle1">请填写您所参演过的剧/栏目名称及所饰演角色</p>
				<p class="starTitle2">至少添加一项，也可添加多个</p>
				<div class="showBoxContent">				
					<div v-for="(data,index) in showBox" :key="index" class="showBox clearfix">
						<div class="showBox1">{{data.lanmu}}</div>
						<div class="showBox2">{{data.play}}</div>					
						<img @click="delShowBox(index,$event)" class="delShow" src="../../../assets/images/49-50/j-1.png"/>
					
					</div>					
				</div>
				<div class="showInput">
					<input class="showInput1" v-focus="['剧/栏目名称','']" type="text" placeholder="剧/栏目名称"/>
					<input class="showInput2" v-focus="['饰演角色','']" type="text" placeholder="饰演角色"/>
				</div>
				
				<div class="addName">
					<img src="../../../assets/images/49-50/t-1.png"/>
					<span @click ="addShowBox" class="addBtn">添加一条新的</span>
				</div>
				
				<div class="submit">
					<p>点击提交表示同意<a href="javascript:;">保密协议</a></p>
				</div>
				<div class="submitBtn">
					<span>提交</span>
				</div>
				
			</div>
		</div>
	</div>
</template>

<script>
	import MineHead from '../../../comments/mineHead';
	export default{
		name:"mineIndex",
		data(){
			return{
				title:{
					names:'明星认证',
					paths:'mine'
				},
				showBox:[
					{
						lanmu:'来自房上的你',
						play:'房梁仙子'
					}
				]
			}
		},
		components:{
			MineHead
		},
		methods:{
			addShowBox(event){
				var showInput= event.target.parentNode.previousSibling.previousSibling;								
				var showBox1 = showInput.children[0].value;
				var showBox2 = showInput.children[1].value;				
				if(showBox1 !='' && showBox2 !=''){
					this.showBox.push({lanmu:showBox1,play:showBox2})
				}
				showInput.children[0].value = '';
				showInput.children[1].value = '';			
				
			},
			delShowBox(num,event){				
				var showBoxContent= event.target.parentNode.parentNode;
				if(num!=0){
					showBoxContent.removeChild(event.target.parentNode);
				}
				
			}
		},
		computed:{
			
		}
	}
</script>

<style lang="less">
.mineStarAuten{
	width:10rem;
	font-size:16/64rem;	

	.starAutenContainer{		
		
		.starMiddle{
			padding: 0 20/64rem;
			.starInput{
				input{
					width:10-40/64rem;
					height:76/64rem;
					border-radius:10/64rem;
					font-size: 28/64rem;
					color:#a9a2b4;
					padding-left:18/64rem;
					border:2px solid #a9a2b4;	
					box-sizing: border-box;
					outline: none;			
				}
				&>div{
					position: relative;
				}
				&>div span{
					position:absolute;
					top:46/64/2rem;
					right:46/64rem;
					font-size: 26/64rem;
					color:#a54be8;
				}			
				.starInput1{
					margin:20/64rem 0 20/64rem 0;
				}									
			}
			.starContent{
				width:10-40/64-36/64rem;
				height:372/64-24*2/64rem;
				background:#e5e0ed;
				border-radius: 15/64rem;
				margin:20/64rem 0;				
				
				padding:24/64rem 18/64rem;
				.title{
					font-size: 28/64rem;
					color:#313131;
					margin-bottom: 26/64rem;
				}
				& div{
					float: left;
				}
				.srcLeft{
					img{
						width:180/64rem;
						height:250/64rem;
						margin-right:114/64rem;
						padding-left:(62-18)/64rem;
					}
				}
				.srcRight{
					position: relative;
					
					img{
						width:180/64rem;
						height:250/64rem;
					}
					.upload{
						position: absolute;
						width:114/64rem;
						font-size: 27/64rem;
						line-height: 34/64rem;
						text-align: center;
						color:#A54BE8;
						top:160/64rem;
						right:35/64rem;
					}
				}
			}
			.starTitle1{
				font-size: 28/64rem;
				color:#313131;
				padding-top:22/64rem;				
				border-top:1/64rem solid #313131;
				font-weight: bold;
				margin-bottom: 10/64rem;
			}
			.starTitle2{
				font-size: 28/64rem;								
				color:#a54be8;				
				border-bottom: 24/64rem;
			}
			.showBox{
				position: relative;
				div{
					float: left;
					height:76/64rem;
					line-height: 76/64rem;
					font-size: 28/64rem;
					color:#313131;
					border-radius: 10/64rem;
					margin-top:24/64rem;
					padding-left: 18/64rem;
					border:2px solid #d1cbdc;
				}
				.delShow{
					position: absolute;
					bottom:(78-52)/2/64rem;
					width:52/64rem;
					height:52/64rem;
					right:16/64rem;
				}
				.showBox1{
					width:328/64rem-18/64rem;
					margin-right:10/64rem;
										
				}
				.showBox2{
					width:168/64rem-18/64rem;
					
				}
				
			}
			.showInput{
				margin-top:20/64rem;
				input{
					height:76/64rem;					
					font-size: 28/64rem;
					color:#a9a2b4;
					border-radius: 10/64rem;
					outline: none;
					border:2px solid #d1cbdc;
					padding-left:18/64rem;
				}
				.showInput1{
					
					width:366/64rem-18/64rem;
					margin-right:10/64rem;
				}
				.showInput2{
					width:208/64rem-18/64rem;
				}
			}
			.addName{
				position: relative;
				margin-top:20/64rem;
				
				img{
					width:10-40/64rem;
					height:90/64rem;
				}
				span{
					position: absolute;
					top:32/64rem;
					left:225/64rem;
					font-size:28/64rem;
					color:#a9a2b4;
					
				}
				
			}
			.submit{
				text-align: center;
				font-size: 28/64rem;
				color:#a9a2b4;
				margin:160/64rem 0 66/64rem 0;
				a{
					color:#a54be8;
					font-size: 28/64rem;
					text-decoration: underline;
					padding-left: 5/64rem;
				}
			}
			.submitBtn{
				margin-bottom: 30/64rem;
				text-align: center;
				height:100/64rem;
				line-height:100/64rem;
				background:url('../../../assets/images/49-50/按钮-1.png') no-repeat 0 0;
				background-size:10-40/64rem 100/64rem;
				span{
					font-size: 35/64rem;
					color:#fff;
				}
			}			
		}		
	}
}	
</style>


