<template>
	<div class="mineOpinionIndex">
		<MineHead :getTitle="title"/>
		<span @click="getContent" class="changeText">提交</span>
		<div class="text">
			<textarea v-focus="['请输入您的宝贵意见...','']" resize="none" placeholder="请输入您的宝贵意见..."></textarea>
		</div>
		<p>您的支持和建议将是我们前进的动力！</p>
	</div>
</template>

<script>
	import MineHead from '../../../comments/mineHead';
	export default{
		name:'opinionIndex',
		data(){
			return{
				title:{
					names:'意见反馈',
					method:'提交',
					paths:'mine'
				}
			}
		},
		methods:{
			getContent(event){
				
			}
		},
		components:{
			MineHead
		}
	}
</script>

<style lang="less">
	.mineOpinionIndex{	
		position:relative;
		width:10rem;
		.changeText{
			font-size: 32/64rem;
			color:#ffffff;					
			position: absolute;
			right:40/64rem;
			top:40/64rem;
		}
		.text{
			margin-top:30/64rem;
			textarea{
				width:10-80/64rem;
				padding:10/64rem 40/64rem;
				height:876/64rem;
				border:none;				
				outline: none;
				border-bottom: 1/64rem solid #d7d2e0;
				border-top: 1/64rem solid #d7d2e0;
				font-size: 28/64rem;
				color:#b5b0bf;
			}
		}
		p{
			font-size: 28/64rem;
			color: #B5B0BF;
			text-align: center;
			line-height: 75/64rem;
		}
		
	}
</style>


