<template>
	<div class="minePrefecture">
		<MineHead :getTitle="title"/>
		<div class="prefectureContent">
			<p class="title">当前城市</p>
			<div class="current">哈尔滨</div>
			<p class="title">热门城市</p>			
			<ul class="hotCity">
				<li @click="getHotCity(index,$event)" v-for="(data,index) in hotCity" :key="index">{{data.city}}</li>				
			</ul>
			<p class="title">全部城市</p>
			<ul class="allCity">
				<li @click="getAllCity(index,$event)" v-for="(data,index) in allCity" :key="index">{{data.city}}</li>				
			</ul>
		</div>
	</div>
</template>

<script>
	import MineHead from '../../../comments/mineHead';
	export default{
		name:'minePrefecture',
		data(){
			return{
				title:{
					names:'设置地区',					
					paths:'mine/minePersonalData'
				},
				hotCity:[
					{
						city:'北京'
					},
					{
						city:'上海'
					},
					{
						city:'深圳'
					},
					{
						city:'香港'
					}					
				],
				allCity:[
					{
						city:'我国'
					},
					{
						city:'战斗民族'
					},
					{
						city:'AV国'
					},
					{
						city:'泡菜国'
					}					
				]
			}
		},
		components:{
			MineHead
		},
		methods:{
			getHotCity(num,event){
				document.querySelector('.current').innerText = event.target.innerText;
			},
			getAllCity(num,event){
				document.querySelector('.current').innerText = event.target.innerText;
			}
		}
	}
</script>

<style lang="less">
	.minePrefecture{
		width:10rem;
		.prefectureContent{
			.title{
				padding-left:34/64rem;				
				height:40/64rem;
				line-height:40/64rem;
				color:#a54be8;
				background:#e5e0ed;
				font-size:24/64rem;
			}
			.current{
				height:80/64rem;
				line-height: 80/64rem;
				font-size: 30/64rem;
				color:#000000;
				padding-left:34/64rem;

			}
			ul{
				padding-left:20/64rem;
				li{
					height:80/64rem;
					padding-left:14/64rem;
					line-height:80/64rem;
					color:#000000;
					font-size: 30/64rem;
					border-bottom:1/64rem solid #d7d2e0;
				}				
			}
			.hotCity{
				li:last-child{
					border:none;
				}
			}
		}
		
	}
</style>


