<template>
	<div class="mineChangeBinding">		
		<MineSetBtnHead :getTitle="title"/>	
		
		<div class="changeContent">			
			<input v-focus="['请输入要绑定的手机号','']" class="change1" type="text" placeholder="请输入要绑定的手机号"/>
			<input v-focus="['请输入验证码','']" class="change2" type="text" placeholder="请输入验证码"/>
			<span class="change3">获取验证码</span>
			<input v-focus="['请输入登录密码','']" class="change4" type="text" placeholder="请输入登录密码"/>			
		</div>
	</div>
</template>

<script>
	import MineSetBtnHead from '../../../comments/mineSetBtnHead';
	
	import Vue from 'vue';
	//使用全局的自定义指令(获取焦点时把默认显示的内容清空，失去焦点时显示默认内容)
	Vue.directive('focus',{
		
		inserted:function(element,arg){
			var arg = arg.value;
			var add = arg[0];
			var del = arg[1];
			
			element.onclick= function(){
				
				this.setAttribute('placeholder',del);
				
			}
			element.onblur =function(){
				this.setAttribute('placeholder',add);
			}			
			
		}
	})
	
	export default{
		name:'mineChangeBinding',
		data(){
			return{
				title:{
					names:'修改绑定',
					method:'修改',
					paths:'mineSetBtn'
				}
			}
		},
		methods:{
			
		},
		components:{
			MineSetBtnHead
		}
		
		
	}
</script>

<style lang="less">
	.mineChangeBinding{
		width:10rem;
		position:relative;		
		.changeContent{
			width:10-40/64rem;
			padding:0 20/64rem;
			input{
				margin-top:20/64rem;
				width:100%;
				height:76/64rem;
				outline: none;
				border:2px solid #a9a2b4;	
				box-sizing: border-box;
				border-radius:10/64rem;
				outline: none;
				font-size: 28/64rem;
				color:#A9A2B4;
				padding-left:18/64rem;			
			}
			.change2{
				width:320/64rem;
				margin-right:10/64rem;
			}
			.change3{
				float: right;
				color:#A54BE8;
				display:block;
				width:256/64rem;
				height:70/64rem;
				border-radius:10/64rem;
				border:2/64rem solid #A54BE8;
				margin-top:20/64rem;
				text-align: center;
				font-size: 28/64rem;
				line-height: 70/64rem;
			}
		}
		
	}
</style>


