<template>
	<div class="magic">
		<div class="title">
			<p>魔法触恋 第一集</p>
			<router-link :to="{path:'/flower'}">
				<img src="../../../../assets/images/4/f-1.png"  alt="">
			</router-link>
		</div>
		<div class="video">
			<video id="my-video" class="video-js" controls preload="auto" 
			poster="http://vjs.zencdn.net/v/oceans.png" data-setup="{}">
				<source src="http://vjs.zencdn.net/v/oceans.mp4" type="video/mp4">
				<source src="http://vjs.zencdn.net/v/oceans.webm" type="video/webm">
				<source src="http://vjs.zencdn.net/v/oceans.ogg" type="video/ogg">


					To view this video please enable JavaScript, and consider upgrading to a web browser that
					<a href="http://videojs.com/html5-video-support/" target="_blank">supports HTML5 video</a>
				</p>
			</video>
		</div>
		<div class="content">
			<ul class="nav">
				<router-link active-class="active" tag='li' :to="{path:'/flower/tv/magic/'+nav.id}" v-for="(nav,index) in navList">{{nav.title}}</router-link>	
			</ul>
			<router-view></router-view>
		</div>

	</div>
</template>
<script>
	export default {
		name:"circle",
		data() {
			return {
				navList:[
					{
						title:"神评论",
						id:"comment"
					},
					{
						title:"内容简介",
						id:"intro"
					},
					{
						title:"选集",
						id:"sel"
					}
				]
			}
		},
		methods:{
/*			//使用dom取出source元素  
			var srcs=document.getElementsByTagName("source");  
			//设置你想改变的那个source元素的src属性  
			/这里面的i代表source的角标  
			srcs[i].src="xxxxx.xxx"; */
		}
	}
</script>

<style lang="less" scoped>
	.magic {
		width:10rem;
		height:100%;
		position: relative;
	}

	.magic .title {width: 10rem;height:90/64rem;position: absolute;z-index: 2;}
	.magic .title p {font-size: 32/64rem;color:#fff;line-height: 90/64rem;text-align: center;}
	.magic .title img {width: 21/64rem;height: 40/64rem;position: absolute;top:4/64rem;left:20/64rem;padding: 20/64rem;}


	#my-video {
		width:10rem;
		height:350/64rem;
	}
	.video {
		height:350/64rem;
		background: black;
	}
	.content {
		margin:20/64rem;
	}
	.nav {
		height:60/64rem;
		width:600/64rem;
		margin:0 auto;
		list-style: none;
		// border-radius: 20px;
		// border:1/64rem solid rgb(165,75,232);
	}
	.nav li {
		float:left;
		height:60/64rem;
		width:198/64rem;
		line-height: 60/64rem;
		text-align: center;
		font-size: 26/64rem;
		color:#aa5ae9;
		border:1/64rem solid #aa5ae9;
	}

	.nav .active {
		background: #aa5ae9;
		color:white;
	}

</style>


