<template>
	<div class="mine">
		<!--头部大照片部分-->
		<div class="mineHeader">

		</div>
		<!--设置按钮-->
		<router-link :to="{path:'/mine/mineSetBtn'}">
			<div class="set">
				<img src="../../assets/images/47/profile_set_icon_normal.png" />

			</div>
		</router-link>

		<!--头部个性签名部分-->
		<div class="box">
			<div class="center">
				<img :src="minePersonal.img" alt="" />
			</div>
			<div class="content">
				<p class="cont1">{{minePersonal.userName}}</p>
				<p class="cont2">ID:{{minePersonal.ofansID}}</p>
				<p class="cont3">个性签名：{{minePersonal.personsign}}</p>

			</div>
			<div class="bottom clearfix">
				<div class="bottomLeft">我的二维码</div>
				<router-link :to="{path:'/mine/minePersonalData'}">
					<div class="bottomRight">编辑资料</div>
				</router-link>
			</div>
		</div>
		<!--中间添加照片部分-->
		<div class="mineContent">
			<img @click="clickAdd" v-if="mineShow" src="../../assets/images/47/profile_photo_add_normal.png" alt="" />
			<img @click="addPhoto" v-else class="mindAdd" src="../../assets/images/55/j-1.png"/>
			<div class="photo">照片集空空的，快点儿添加吧^_^</div>
		</div>
		<!--关于偶粉列表部分-->
		<div class="mineBottom">
			<ul :class="[{'show':isDorp}]">
				<li>关于偶粉<span></span></li>
				<router-link tag="li" :to="{path:'/mine/mineOpinionIndex'}">
					意见反馈<span></span>
				</router-link>
				<li @click="getAuthen">我要认证<span></span></li>
			</ul>
		</div>
		<!--点击我要认证列表   弹出认证框-->

		<div class="authen" v-show="isDorp">
			<div class="authenContent">
				<div class="autenHead clearfix">
					<!--跳转到明星认证页面-->
					<router-link class="autenHeadPng1" tag="div" :to="{path:'/mine/mineStarAuten'}">
						
						<img class="img1" src="../../assets/images/47/明星-1.png" />
						<p>明星认证</p>
						
					</router-link>

					<div class="autenHeadPng2">
						<img src="../../assets/images/47/制片-1.png" />
						<p>制片方认证</p>
					</div>
				</div>
				<div class="authenFoot" @click="getAuthen">取消</div>
			</div>
		</div>

	</div>
</template>
<script>
import $ from 'jquery'

	export default {
		name: "mine",
		data() {
			return {
				isDorp: false,
				mineShow:true,
				minePersonal:{
					"id":8,    
			        "ofansID": "03452100", 
			        "img": "./static/img/timg.jpg",   
			        "twocode": "",   
			        "userName": "不一样的烟火",  
			        "userPicList":[
			        	"./static/img/timg.jpg",
			        	"./static/img/timg.jpg",
			        	"./static/img/timg.jpg"
			        ],
			        "personsign": "人生何时才是尽头，我的春天在何处，是否还有在我身边..."     
				}
			}
		},
		created(){
			
			this.$axios.get("/mineIndex").then(res=>{
//				console.log(res.data.mineIndex[1]);
				
				this.minePersonal = res.data.mineIndex[1];
				
			})
		},
		components: {


		},
		methods: {
			getAuthen(event) {
				this.isDorp = !this.isDorp;
			},
			clickAdd(event){
				this.mineShow = !this.mineShow;
				var getPhoto = event.target.nextSibling.nextSibling;
//				console.log(getPhoto);
				if(getPhoto.innerText =='照片集空空的，快点儿添加吧^_^'){
					getPhoto.innerHTML = '';
				}
				var src = document.createElement('img');
				src.setAttribute('src',this.minePersonal.userPicList[0]);
				getPhoto.appendChild(src);
				
			},
			addPhoto(event){
				var src = document.createElement('img');
				var minePhoto = event.target.nextSibling.nextSibling;
				
				if(minePhoto.children.length<3){
					var i = minePhoto.children.length;
					src.setAttribute('src',this.minePersonal.userPicList[i]);
					minePhoto.appendChild(src);
					
				}else{
					alert('只能添加三张照片');
				}
				
				
			}

		}
	}
</script>

<style lang="less">
	.mine {
		width: 10rem;
		height: 100%;
		position: relative;
		margin-bottom:100/64rem;
		.set {
			position: absolute;
			width: 45/64rem;
			height: 45/64rem;
			top: 60/64rem;
			right: 50/64rem;
			z-index: 10000;
			img {
				width: 45/64rem;
				height: 45/64rem;
			}
		}
		.mineHeader {
			width: 10rem;
			height: 580/64rem;
			background: url('../../assets/images/47/timg.jpg') no-repeat center center;
			background-size: 10rem 580/64rem;
			background-size: cover;
			filter: blur(8/64rem);
			z-index: 100;
		}
		.mineHeader:before {
			content: '';
			position: absolute;
			top: 0;
			width: 100%;
			height: 580/64rem;
			/*background-color: rgba(0,0,0,0.4);*/
		}
		.box {
			width: 10rem;
			position: absolute;
			text-align: center;
			top: 0;
			z-index: 1000;
			.center {
				margin-top: 110/64rem;
				margin-left: 232/64rem;
				width: 170/64rem;
				height: 170/64rem;
				border: 5px solid #E4DBD7;
				border-radius: 50%;
				overflow: hidden;
				img {
					width: 170/64rem;
					height: 170/64rem;
				}
			}
			.content {
				p {
					margin-top: 10/64rem;
				}
				.cont1 {
					font-size: 27/64rem;
					color: white;
					margin-top: 10/64rem;
				}
				.cont2 {
					font-size: 25/64rem;
					color: #d8cdc7;
				}
				.cont3 {
					font-size: 25/64rem;
					color: #d8cdc7;
					text-align: left;
					padding: 0 60/64rem;
					line-height: 30/64rem;
					margin-top: 20/64rem;
				}
			}
		}
		.bottom {
			position: absolute;
			bottom: -140/64rem;
			height: 100/64rem;
			width: 10rem;
			/*background:rgba(129,81,65,0.4);*/
			background: rgba(0, 0, 0, 0.2);
			div {
				float: left;
				text-align: center;
				font-size: 30/64rem;
				line-height: 100/64rem;
				padding-left: (47+26+60)/64rem;
				color: #ffffff;
			}
			.bottomLeft {
				background: url('../../assets/images/47/profile_erweima_icon.png') no-repeat center 0;
				background-position: 60/64rem;
				position: relative;
			}
			.bottomLeft:after {
				content: '';
				width: 2/64rem;
				height: 80/64rem;
				background: rgba(0, 0, 0, 0.5);
				position: absolute;
				top: 10/64rem;
				right: -30/64rem;
			}
			.bottomRight {
				background: url('../../assets/images/47/profile_edit_icon.png') no-repeat center 0;
				background-position: 60/64rem;
			}
		}
		.mineContent {
			height: 152/64rem;
			width: 10-(20/64*2)rem;
			margin: 20/64rem;
			border: 1px solid #d7d2e0;
			background: #FAF9F8;
			color: #d1cbdc;
			position: relative;
			border-radius: 5/64rem;
			&>img {
				position: absolute;
				z-index: 1000;
				right: 15/64rem;
				top: 15/64rem
			}
			.mindAdd{
				top:(152-35)/2/64rem;
				right:34/64rem;
				z-index:2000;
			}			
			.photo{
				font-size: 30/64rem;
				line-height: 152/64rem;
				padding-left: 16/64rem;				
				img{
					float:left;
					width:120/64rem;
					height:120/64rem;							
					/*top:15px;
					left:15px;*/
					margin:15px;
				}
				
			}
		}
		.mineBottom {
			margin-bottom:10/64rem;
			.show {
				background: #f2eef9;
			}
			ul {
				border-top: 1px solid #d7d2e0;
				border-bottom: 1px solid #d7d2e0;
				background: #FFFFFF;
				li {
					height: 90/64rem;
					line-height: 90/64rem;
					border-bottom: 1px solid #d7d2e0;
					margin-left: 50/64rem;
					padding-left: 74/64rem;
					font-size: 36/64rem;
					color: #1b1b1b;
					font-family: "微软雅黑";
					span {
						display: block;
						width: 18/64rem;
						height: 30/64rem;
						float: right;
						margin-top: 32/64rem;
						margin-right: 32/64rem;
						background: url('../../assets/images/47/插入箭头.png') no-repeat center center;
					}
				}
				li:last-child {
					border: none;
				}
				li:nth-child(1) {
					background: url('../../assets/images/47/profile_about_icon.png') no-repeat 0 center;
				}
				li:nth-child(2) {
					background: url('../../assets/images/47/opinion.png') no-repeat 0 center;
				}
				li:nth-child(3) {
					background: url('../../assets/images/47/profile_cerify_icon.png') no-repeat 0 center;
				}
			}
		}
		.authen {
			width: 10rem;
			height:50%;
			position: absolute;
			bottom:-80/64rem;
			z-index:10010;			
			background: rgba(237, 237, 237, 0.4);
			.authenContent {
				position: absolute;
				width: 10-40/64rem;
				margin: 0 20/64rem;
				padding-bottom:20/64rem;
				bottom:0rem;
				z-index: 10020;
				.autenHead {
					width: 100%;
					height: 200/64rem;
					background: #FFFFFF;
					border-radius: 15/64rem;
					text-align: center;
					img {
						width: 102/64rem;
						height: 102/64rem;
						margin: 24/64rem 0 20/64rem 0;
					}
					&>div {
						text-align: center;
					}
					&>div p {
						font-size: 30/64rem;
						color: #434343;
						font-family: 'simsun';
						font-weight: bold;
					}
					.autenHeadPng1 {
						width: 130/64rem;
						float: left;
						margin-left: 110/64rem;
					}
					.autenHeadPng2 {
						float: right;
						width: 160/64rem;
						margin-right: 110/64rem;
					}
				}
				.authenFoot {
					width: 100%;
					height: 90/64rem;
					line-height: 90/64rem;
					margin-top: 12/64rem;
					font-size: 30/64rem;
					color: #a54be8;
					background: #ffffff;
					font-weight: bold;
					border-radius: 15/64rem;
					text-align: center;
				}
			}
		}
	}
</style>


