<template>
	<div class="minePersonalData">
		<MineHead :getTitle="title" />
		<div class="personalContent">
			<ul class="first clearfix">
				<router-link v-for="(data,index) in personalData1" tag="li" :to="{path:'/mine/minePersonalData/'+data.id}" :key="index">
					{{data.personalTitle}}
					<i></i>
					<p @click="setPhoto(index,$event)">{{data.personalContent}}</p>
				</router-link>
			</ul>
			<ul class="second clearfix">
				<li @click="getSex">性别<i></i>
					<p>{{showSex? '女':'男'}}</p>
				</li>
				<router-link v-for="(data,index) in personalData2" tag="li" :to="{path:'/mine/minePersonalData/'+data.id}" :key="index">
					{{data.personalTitle}}<i></i>
					<p>{{data.personalContent}}</p>
				</router-link>
			</ul>
		</div>
		<div class="authen" v-show="isDorp">
			<div class="authenContent">
				<div class="autenHead clearfix">
					<div @click="setMowenSex" class="autenHeadPng1">
						<img class="img1" src="../../../assets/images/66/1-1.png" />
						<p>女神</p>
					</div>

					<div  @click="setMenSex" class="autenHeadPng2">
						<img src="../../../assets/images/66/2-1.png" />
						<p>型男</p>
					</div>
				</div>
				<div class="authenFoot" @click="getSex">取消</div>
			</div>
		</div>
	</div>
</template>

<script>
	import MineHead from '../../../comments/mineHead';
	export default {
		name: 'minePersonalData',
		data() {
			return {
				isDorp: false,
				showSex:true,
				title: {
					names: '个人资料',
					paths: 'mine'
				},
				personalData1: [{
						personalTitle: '头像',
						personalContent: '',
						id: ''
					},
					{
						personalTitle: '昵称',
						personalContent: '不一样的烟火',
						id: 'mineNickName'
					},
					{
						personalTitle: '偶粉号',
						personalContent: '15104595566',
						id: ''
					},
					{
						personalTitle: '手机号',
						personalContent: '13613652693',
						id: ''
					}
				],
				personalData2: [

					{
						personalTitle: '地区',
						personalContent: '北京  崇文区',
						id: 'minePrefecture'
					},
					{
						personalTitle: '个性签名',
						personalContent: '人生如寄，感谢给与我一切的人、事、物',
						id: 'mineSignature'
					}
				]				
				
			}
		},
		created(){
			this.$axios.get('/mineIndex?userid=6').then(res=>{
				var arr = res.data.mineIndex;
				
				for(var i = 0;i<arr.length;i++){
					if(arr[i].id == 6){			
						
						this.personalData1 = [{
								personalTitle: '头像',
								personalContent: '',
								id: ''
							},
							{
								personalTitle: '昵称',
								personalContent:arr[i].userName,
								id: 'mineNickName'
							},
							{
								personalTitle: '偶粉号',
								personalContent:arr[i].ofansID,
								id: ''
							},
							{
								personalTitle: '手机号',
								personalContent:arr[i].phone,
								id: ''
							}
						]
						
						this.personalData2 = [

							{
								personalTitle: '地区',
								personalContent: '北京  崇文区',
								id: 'minePrefecture'
							},
							{
								personalTitle: '个性签名',
								personalContent:arr[i].personsign,
								id: 'mineSignature'
							}
						]				
						
					}					
				}
			
				
			})
		},
		components: {
			MineHead
		},
		methods: {
			getSex(event) {
				this.isDorp = !this.isDorp;
			},
			setMowenSex(event){
				this.showSex = true;
				this.isDorp = !this.isDorp;
			},
			setMenSex(event){
				this.showSex = false;
				this.isDorp = !this.isDorp;
			},
			setPhoto(num,event){
				
			}
		}
	}
</script>

<style lang="less">
	.minePersonalData {
		width: 10rem;
		height: 100%;		
		position: relative;
		.personalContent {
			ul {
				margin: 30/64rem 0 40/64rem 0;
				border-top: 1/64rem solid #d7d2e0;
				border-bottom: 1/64rem solid #d7d2e0;
				padding: 0 20/64rem 0 30/64rem;
				background: #ffffff;
				li {
					border-bottom: 1/64rem solid #d7d2e0;
					height: 90/64rem;
					line-height: 90/64rem;
					font-size: 36/64rem;
					padding-left: 10/64rem;
					color: #1B1B1B;
					i {
						display: block;
						width: 18/64rem;
						height: 30/64rem;
						float: right;
						margin-top: 32/64rem;
						margin-right: 10/64rem;
						background: url('../../../assets/images/47/插入箭头.png') no-repeat center center;
					}
					p {
						float: right;
						font-size: 28/64rem;
						color: #707070;
						margin-right: 20/64rem;
					}
				}
				li:last-child {
					border: none;
				}
			}
			.first {
				li:first-child {
					height: 130/64rem;
					line-height: 130/64rem;
					i {
						margin-top: 50/64rem;
					}
					p {
						width: 80/64rem;
						height: 80/64rem;
						margin-top: (130-80)/2/64rem;
						border-radius: 50%;
						background: url('../../../assets/images/47/timg.jpg') no-repeat center center;
						background-size: 80/64rem 80/64rem;
					}
				}
			}
			.second {
				li:last-child {
					height: 130/64rem;
					line-height: 130/64rem;
					i{
					margin-top:50/64rem;
					}
					p {
						height: 66/64rem;
						width: 312/64rem;
						line-height: 33/64rem;
						margin-top: (130-66)/2/64rem;
						overflow: hidden;
					}
				}
			}
		}
		.authen {
			width:10rem;
			height: 100%;
			position: absolute;
			top:100/64rem;	
			background:rgba(237,237,237,0.4);		
			.authenContent {
				position: absolute;
				width: 10-40/64rem;				
				margin: 0 20/64rem;
				bottom:0rem;
				z-index: 1000;				
				.autenHead {
					width: 100%;
					height: 200/64rem;
					background: #FFFFFF;
					border-radius: 15/64rem;
					text-align: center;
					img {
						width: 102/64rem;
						height: 102/64rem;
						margin: 24/64rem 0;
					}
					&>div {
						text-align: center;
						width: 102/64rem;
					}
					&>div p {
						font-size: 30/64rem;
						color: #434343;
						font-family: 'simsun';
						font-weight: bold;
						
					}
					.autenHeadPng1 {
						float: left;
						margin-left: 110/64rem;
					}
					.autenHeadPng2 {
						float: right;
						margin-right: 110/64rem;
					}
				}
				.authenFoot {
					width: 100%;
					height: 90/64rem;
					line-height: 90/64rem;
					margin-top: 12/64rem;
					font-size: 30/64rem;
					color: #a54be8;
					background: #ffffff;
					font-weight: bold;
					border-radius: 15/64rem;
					text-align: center;
				}
			}
		}
	}
</style>


