<template>
	<div class="circleHair">
		<div class="circleHairHead">
			<router-link :to="{path:'/circle'}">
				<div class="circleHairCancel">取消</div>
			</router-link>
			<div class="title">发圈子</div>
			<router-link :to="{path:'/circle'}">
				<div class="send" @click="win">发送</div>
			</router-link>
		</div>
		<div class="circleHairCon">
			<div class="text">
				<textarea resize="none" placeholder="说点什么吧...."></textarea>
			</div>
			<div class="circleHairPicture">
				<label for="fi">
					<input type="file" name="fi" id="fi" value=""/>
					<img src="../../../assets/images/29-30-34/a1.png" alt="" />
				</label>
			</div>
		</div>
		<div class="circleHairFooter">
			<div class="circleHairLocation" @click="Record">
				<img class="circleHairRight" src="../../../assets/images/29-30-34/插入箭头.png" alt="" />
				<img :src="none.flag ? model.src2 : model.src1 " alt="" />
				<span>{{none.flag ? "三里屯路" : "插入位置"}}</span>
			</div>
			<div class="circleHairRecord" @click="circleHairShow">
				<img src="../../../assets/images/29-30-34/2-1.png" alt="" />
				<span>插入录音</span>
			</div>
			<div class="circleHairRecords" v-show="none.flag1">
				<div class="circleHairRecordsBox">
					<div class="circleHairRecordsTop">
						<img src="../../../assets/images/31-32-33/z3.png" alt="" />
						<img src="../../../assets/images/31-32-33/1-2.png" alt="" />
						<img src="../../../assets/images/31-32-33/y3.png" alt="" />
					</div>
					<div class="circleHairRecordsCon">松开停止录音</div>
					<div class="circleHairRecordsBottom">
						<div class="circleHairRecordsBottomCancel">取消</div>
						<div class="circleHairRecordsBottomSend">确定</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import png1 from '../../../assets/images/29-30-34/1-1.png'
	import png2 from '../../../assets/images/29-30-34/1-2.png'
	export default {
		name: "circleHair",
		data() {
			return {
				model: {
					hoad: "想和大家说...",
					src1: png1,
					src2: png2
				},
				none: {
					flag: false,
					flag1: false
				}
			}
		},
		methods: {
			Record() {
				this.none.flag = !this.none.flag;
			},
			circleHairShow() {
				this.none.flag1 = !this.none.flag1;
				console.log(this.none.flag1)
			},
			win() {
				alert("发送成功");
			}
		}
	}
</script>

<style lang="less">
	.circleHair {
		.circleHairHead {
			width: 100%;
			height: 90/64rem;
			background: #a54be8;
			div {
				display: inline-block;
				font-size: 32/64rem;
				line-height: 90/64rem;
			}
			.circleHairCancel,
			.title {
				color: #fff;
			}
			.title {
				width: 445/64rem;
				text-align: center;
			}
			.circleHairCancel {
				margin-left: 32/64rem;
			}
			.send {
				color: #fff100;
			}
		}
		.circleHairCon {
			position: relative;
			.text {
				textarea {
					width: 10-80/64rem;
					padding: 10/64rem 40/64rem;
					height: 380/64rem;
					border: none;
					outline: none;
					border-bottom: 1/64rem solid #d7d2e0;
					border-top: 1/64rem solid #d7d2e0;
					font-size: 28/64rem;
				}
			}
			.circleHairPicture {
				position: absolute;
				bottom: 32/64rem;
				left: 40/64rem;
				#fi{
					display: none;
				}
			}
		}
		.circleHairFooter {
			.circleHairLocation,
			.circleHairRecord {
				border: 1px solid #d1cbdc;
				border-radius: 15/64rem;
				width: 560/64rem;
				margin: 0 auto;
				height: 80/64rem;
				img {
					margin: 15/64rem 0 0 20/64rem;
					vertical-align: top;
				}
				span {
					line-height: 80/64rem;
					font-size: 36/64rem;
					margin-left: 24/64rem;
					color: #7b7781;
				}
			}
			.circleHairLocation {
				position: relative;
				margin: 30/64rem 0 0 40/64rem;
				.circleHairRight {
					top: 10/64rem;
					right: 24/64rem;
					position: absolute;
				}
			}
			.circleHairRecord {
				margin: 20/64rem 0 0 40/64rem;
			}
			.circleHairRecords {
				.circleHairRecordsBox {
					margin: 20/64rem 0 0 40/64rem;
				}
				.circleHairRecordsCon {
					text-align: center;
					margin-top: 20/64rem;
					font-size: 30/64rem;
					width: 535/64rem;
					color: #7d7d7d;
				}
				.circleHairRecordsBottom {
					margin-top: 10/64rem;
					.circleHairRecordsBottomCancel,
					.circleHairRecordsBottomSend {
						display: inline-block;
						width: 290/64rem;
						height: 90/64rem;
						background: #fff;
						font-size: 30/64rem;
						color: #A54BE8;
						text-align: center;
						line-height: 90/64rem;
						border-radius: 10/64rem;
					}
					.circleHairRecordsBottomCancel {
						margin-left: -30/64rem;
					}
					.circleHairRecordsBottomSend {
						margin-left: 20/64rem;
					}
				}
			}
		}
	}
</style>


