import Vue from 'vue'
import Router from 'vue-router'
import Index from "@/pages/index"
import Flower from "@/pages/index/flower"
import Circle from "@/pages/index/circle"
import Change from "@/pages/index/change"
import Mine from "@/pages/index/mine"
//我的主页的引用部分
import MineStarAuten from "@/pages/index/mine/mineStarAuten.vue"
import MinePersonalData from "@/pages/index/mine/minePersonalData.vue"
import MineNickName from "@/pages/index/mine/mineNickName.vue"
import MinePrefecture from "@/pages/index/mine/minePrefecture.vue"
import MineSignature from "@/pages/index/mine/mineSignature.vue"

import MineSetBtn from "@/pages/index/mine/mineSetBtn.vue"
import MineChangeBinding from "@/pages/index/mine/mineChangeBinding.vue"
import MineChangePassword from "@/pages/index/mine/mineChangePassword.vue"
import MineOpinionIndex from "@/pages/index/mine/mineOpinionIndex.vue"


import CircleDetails from "@/pages/index/circle/circleDetails.vue"
import CircleMaster from "@/pages/index/circle/circleMaster.vue"
import CircleHair from "@/pages/index/circle/circleHair.vue"


import ChangeIndex from "@/pages/index/change/changeIndex.vue"
import ChangePrivate from "@/pages/index/change/changeprivate.vue"
import ChangeAttention from "@/pages/index/change/changeAttention.vue"
import ChangePlays from "@/pages/index/change/changePlays.vue"
import ChangeRichScan from "@/pages/index/change/changeRichScan.vue"
import ChangeSystem from "@/pages/index/change/changeSystem.vue"
import ChangeComment from "@/pages/index/change/changeComment.vue"
import ChangeLetter from "@/pages/index/change/changeLetter.vue"
import ChangeYin from "@/pages/index/change/changeYin.vue"

//金冬路由部分
import JdInit from "@/pages/jdhomePage/jdInit"
import JdSign from "@/pages/jdhomePage/jdSign"
import JdLogn from '@/pages/jdhomePage/jdLogn'
import JdLognyanzheng from '@/pages/jdhomePage/jdLognyanzheng'
import JdFind from '@/pages/jdhomePage/jdFind'
import JdFindperson from '@/pages/jdhomePage/jdFindperson'


//gtl
import Tv from "@/pages/index/tv/tv"
import Magic from "@/pages/index/tv/tvList/magic"
import Comment from "@/pages/index/tv/tvList/tvCon/comment"
import Intro from "@/pages/index/tv/tvList/tvCon/intro"
import Sel from "@/pages/index/tv/tvList/tvCon/sel"
import Allreply from "@/pages/index/tv/tvList/tvCon/allreply"
import Userinfo from "@/pages/index/tv/tvList/tvCon/userinfo"

Vue.use(Router)

export default new Router({
	routes: [
		{
			path:"/",
			name:'index',
			component:Index,
			redirect:"/jdInit",
			children:[
				{
					path:"flower",
					name:'flower',
					component:Flower,

				},
				{
					path:"circle",
					name:'circle',
					component:Circle,
				},
				{
					path:"change",
					name:'change',
					component:Change,
				},
				{
					path:"mine",
					name:'mine',
					component:Mine,
					
				}
			]
		},
		{
			path:"/mine/mineStarAuten",
			name:"mineStarAuten",
			component:MineStarAuten
		},		
		{
			path:"/mine/mineOpinionIndex",
			name:"mineOpinionIndex",
			component:MineOpinionIndex
		},
		{
			path:"/mine/minePersonalData",
			name:"minePersonalData",
			component:MinePersonalData
		},
		{
			path:"/mine/minePersonalData/mineNickName",
			name:"mineNickName",
			component:MineNickName
		},		
		{
			path:"/mine/minePersonalData/minePrefecture",
			name:"minePrefecture",
			component:MinePrefecture
		},
		{
			path:"/mine/minePersonalData/mineSignature",
			name:"mineSignature",
			component:MineSignature
		},
		{
			path:"/mine/mineSetBtn",
			name:"mineSetBtn",
			component:MineSetBtn			
		},
		{
			path:"/mine/mineSetBtn/mineChangeBinding",
			name:"mineChangeBinding",
			component:MineChangeBinding
		},
		{
			path:"/mine/mineSetBtn/mineChangePassword",
			name:"mineChangePassword",
			component:MineChangePassword
		},
		
		{
			path:"/circle/circleDetails",
			name:"details",
			component:CircleDetails
		},
		{
			path:"/circle/circleMaster",
			name:"master",
			component:CircleMaster
		},
		{
			path:"/circle/CircleHair",
			name:"hair",
			component:CircleHair
		},
		{
			path:"/change/changeIndex",
			name:"change",
			component:ChangeIndex,	
			children:[
				{
					path:"/change/changeComment",
					name:'comment',
					component:ChangeComment,
				},
			]
		},
		{
			path:"/change/changeLetter",
			name:'letter',
			component:ChangeLetter,
			children:[
				{
					path:"/change/changeYin",
					name:'yin',
					component:ChangeYin,
				},
			]
		},
			
		{
			path:"/change/changePrivate",
			name:'private',
			component:ChangePrivate,
		
		},
		{
			path:"/change/changeAttention",
			name:'attention',
			component:ChangeAttention,
		},
		{
			path:"/change/changePlays",
			name:'plays',
			component:ChangePlays,
		},
		{
			path:"/change/changeRichScan",
			name:'richScan',
			component:ChangeRichScan,
		},
		{
			path:"/change/changeSystem",
			name:'system',
			component:ChangeSystem,
		},
		
		
//		金冬路由部分
	    {   
			path:"/jdInit",
			name:'jdInit',
			component:JdInit
		   },
		   
		{
			path:"/jdSign",
			name:'jdSign',
			component:JdSign
		   },
		   
		{
			path:"/jdLogn",
			name:'jdLogn',
			component:JdLogn,
		},
		{
			path:"/jdLognyanzheng",
			name:'jdLognyanzheng',
			component:JdLognyanzheng
		},
		{
			path:"/jdFind",
			name:'jdFind',
			component:JdFind
			
		},
		{
			path:"/jdFindperson",
			name:'jdFindperson',
			component:JdFindperson
			
		},

		//gtl片花路由
		{
			path:"/flower/tv",
			name:'tv',
			component:Tv,
			children:[
			{
				path:"magic",
				name:"magic",
				component:Magic,
				redirect:"/flower/tv/magic/comment",
				children:[
				{
					path:"comment",
					name:'comment',
					component:Comment
				},
				{
					path:"intro",
					name:'intro',
					component:Intro
				},
				{
					path:"sel",
					name:'sel',
					component:Sel
				}
				]
			}
			]
		},
		{
			path:"/flower/tv/magic/allreply",
			name:"allreply",
			component:Allreply
		},
		{
			path:"/flower/tv/magic/userinfo",
			name:"userinfo",
			component:Userinfo
		}
		
		
		
	]
})


