	.intro .con li .line4 { margin-top: 20/64rem;width:480/64rem;position: relative;left:120/64rem;}

	.intro .introduction {margin-top:20/64rem;}
	.intro .introduction p {font-size: 26/64rem;line-height: 30/64rem;color:rbg(112,112,112);letter-spacing: 1/64rem;}

</style>


