<template>
	<div class="mineChangePassword">
		<MineSetBtnHead :getTitle="title"/>
		
		<div class="changeContent">			
			<input v-focus="['请输入原密码','']" class="change1" type="text" placeholder="请输入原密码"/>
			<input v-focus="['请设置新密码','']" class="change2" type="text" placeholder="请设置新密码"/>
			<input v-focus="['请再次确认新密码','']" class="change3" type="text" placeholder="请再次确认新密码"/>			
		</div>
		
	</div>
</template>

<script>
	import MineSetBtnHead from '../../../comments/mineSetBtnHead';
	export default{
		name:'mineChangePassword',
		data(){
			return{
				title:{
					names:'修改密码',
					method:'保存',
					paths:'mineSetBtn'
				}
			}
		},
		methods:{
			getContent(event){
				alert('保存成功');				
			}
		},
		components:{
			MineSetBtnHead
		}
	}
</script>

<style lang="less">
	.mineChangePassword{		
		.changeContent{
			width:10-40/64rem;
			padding:0 20/64rem;
			input{
				margin-top:20/64rem;
				width:100%;
				height:76/64rem;
				outline: none;
				border:2px solid #a9a2b4;	
				box-sizing: border-box;
				border-radius:10/64rem;
				outline: none;
				font-size: 28/64rem;
				color:#A9A2B4;
				padding-left:18/64rem;			
			}					
		}
	}
</style>


