<template>

	<div class='jdFlower_box clearfix'>
		<div id='jdFlower_box_left' v-on:click="siginState">
			<div class='jdFlower_box_left  clearfix'>
				<p><img src="../../assets/images/2/menu_logo.png" alt="" class='jdFlower_box_left_logo' /></p>
				<ul v-for="(menu,index) in menuList">
					<li>
						<span><img :src="menu.url1" alt=""  class='jdFlower_box_left_sign'/></span>

					</li>
				</ul>
			</div>
		</div>
		<div id="jdFlower_box_right">
			<!--头部开始-->
			<div class="header">
				<div class="jdIndex_head clearfix">
					<div class='jdIndex_head_menu'>
						<img src="../../assets/images/1/main_menu_normal.png" v-on:click="state" alt="" class='jdIndex_head_menu_img' />
					</div>
					<div class='jdIndex_head_hotebang'>热榜</div>
					<router-link :to="{path:'/jdFind'}">
						<div class='jdIndex_head_big'>
							<img src="../../assets/images/1/main_search_normal.png" alt="" class='jdIndex_head_big_img' />
						</div>
					</router-link>
				</div>

			</div>
			<!--头部结束-->
			<Jdflower/>
			<!--<Footers></Footers>-->
		</div>

		<!--登录开始-->

		<div class="jdFlower_box_Login" id='jdFlower_box_Login_sh'>
			<div class='jdFlower_box_Login_title clearfix'>登录后更精彩哦！</div>
			<router-link :to="{path:'/flower'}">
				<div class='jdFlower_box_Sigbtn'><span>注册</span></div>
			</router-link>
			<router-link :to="{path:'/jdLogn'}">
				<div class='jdFlower_box_Logbtn'><span>登录</span></div>
			</router-link>
			<p class='jdFlower_box_Logbtn_hint'>不用注册，用社交账号直接登录吧！</p>
			<div class="jdFlower_box_Login_signbox">
				<div class='jdFlower_box_Login_wx'></div>
				<div class='jdFlower_box_Login_qq'></div>
				<div class='jdFlower_box_Login_wb'></div>

			</div>
		</div>

		<!--登录结束-->

	</div>

</template>
<script>
	//	import Headers from "../../comments/header"
	import Footers from "../../comments/footer"
	import Jdflower from "../../comments/jdflower"
	import Rebang from "../../assets/images/2/menu_hot_selected.png"
	import Wangju from "../../assets/images/2/menu_netdrama_selected.png"
	import Lanmu from "../../assets/images/2/menu_netprogram_selected.png"
	import Dianying from "../../assets/images/2/menu_movie_selected.png"
	import Dianshiju from "../../assets/images/2/menu_tv_selected.png"
	import Zhenrenxiu from "../../assets/images/2/menu_show_selected.png"
	import Shoucang from "../../assets/images/2/menu_favorite_selected.png"

	
	export default {
		name: "flower",

		data() {
			return {
				
				menuList: [{
						id: 'Rebang',
						url1: Rebang

					},
					{
						id: 'wangju',
						url1: Wangju

					},
					{
						id: 'lanmu',
						url1: Lanmu

					},
					{
						id: 'dianying',
						url1: Dianying

					},
					{
						id: 'dianshiju',
						url1: Dianshiju

					},
					{
						id: 'zhenrenxiu',
						url1: Zhenrenxiu

					},
					{
						id: 'shoucang',
						url1: Shoucang

					},

				],
				sh:false

			}
		},

		methods: {
			state: function(event) {
				this.sh = !this.sh;
				if(this.sh){
					var leftdiv = document.getElementById("jdFlower_box_left");
					var rightdiv = document.getElementById("jdFlower_box_right");
					var jdFlower_box = document.querySelector('.jdFlower_box');
					leftdiv.style.cssText = "left:0;transition: 0.4s ease-in;"
					rightdiv.style.cssText = "left:300px;transition: 0.4s ease-in;"
					jdFlower_box.style.height = '900px;'
				}else{
					var leftdiv = document.getElementById("jdFlower_box_left");
					var rightdiv = document.getElementById("jdFlower_box_right");
					var jdFlower_box = document.querySelector('.jdFlower_box');
					leftdiv.style.cssText = "left:-300px;transition: 0.4s ease-in;"
					rightdiv.style.cssText = "left:0;transition: 0.4s ease-in;"
				}
				
				

				
			},
			siginState: function(event) {
				//      		console.log('点击');
				var sigin = document.getElementById('jdFlower_box_Login_sh');
				sigin.style.display = 'block';

			}

		},

		components: {
			//			Headers,
			Footers,
			Jdflower

		}
	}
</script>

<style lang="less">
	.jdFlower_box {
		/*overflow: hidden;*/
		width:10rem;
		height:1350/75rem;

		position: relative;
	}	
	
	#jdFlower_box_left {
		/*display: none;*/
		width: 300/75rem;
		height:100%;
		background-image: url(../../assets/images/2/menu_bg.png);
		background-size: 300/75rem 100%;
		font-size: 100px;
		text-align: center;
		z-index:11000;
		position:absolute;
		top:0;
		left:-300/75rem;
	}
	
	
	#jdFlower_box_right {
		overflow: hidden;
		width:10rem;
		height:100%;
		position:absolute;
		top:0;
		left:0;
		margin-bottom:100/75rem;
		/*float: left;*/
	}
	
	
	.jdFlower_box_left_logo {
		height: 100/75rem;
		width: 184/75rem;
		margin-top: 80/75rem;
	}
	
	.jdFlower_box_left_sign {
		height: 50/75rem;
		width: 130/75rem;
	}
	
	.jdFlower_box_left ul {
		margin-top: 30/75rem;
		li {
			margin-bottom:0rem;
		}
	}
	/*菜单按钮显示隐藏开始*/
	
	
	/*菜单按钮显示隐藏结束*/
	/*头部样式开始*/
	
	#jdFlower_box_right .header {
		position: fixed;
		top: 0;
		z-index: 10000;
	}
	
	.jdIndex_box {
		width: 750/75rem;
		height: 1334/75rem;
	}
	
	.jdIndex_head {
		width: 750/75rem;
		height: 90/75rem;
		background: rgb(165, 75, 232);
	}	
	
	
	.jdIndex_head_menu,
	.jdIndex_head_hotebang,
	.jdIndex_head_big {
		float: left;
	}
	
	.jdIndex_head_menu_img {
		height: 27/75rem;
		width: 40/75rem;
		margin-left: 30/75rem;
		margin-top: 28/75rem;
		z-index: 5000;
	}
	
	.jdIndex_head_hotebang {
		color: white;
		font-size: 36/75rem;
		margin-top: 28/75rem;
		margin-left: 269/75rem;
	}
	
	.jdIndex_head_big_img {
		height: 37/75rem;
		width: 37/75rem;
		margin-top: 28/75rem;
		margin-left: 269/75rem;
	}
	/*头部样式结束*/
	/*登陆样式开始*/
	
	.jdFlower_box_Login {
		height: 464/75rem;
		width: 615/75rem;
		background: white;
		position: absolute;
		left: 65/75rem;
		top: 390/75rem;
		text-align: center;
		/*z-index: 0;*/
		border-radius: 15px;
		display: none;
		z-index:100000;
	}
	
	.jdFlower_box_Login_title {
		margin-top: 34/75rem;
		color: #434343;
		font-size: 30/75rem;
		margin-bottom: 34/75rem;
	}
	
	.jdFlower_box_Sigbtn,
	.jdFlower_box_Logbtn {
		height: 90/75rem;
		width: 260/75rem;
		float: left;
		margin-left: 30/75rem;
		text-align: center;
		span {
			font-size: 40/75rem;
			font-weight: 400;
			line-height: 90/75rem;
		}
	}
	
	.jdFlower_box_Sigbtn {
		background-image: url(../../assets/images/3/dialog_login_warm_reg_btn_normal.png);
		background-size: 260/75rem 90/75rem;
		color: white;
	}
	
	.jdFlower_box_Logbtn {
		background-image: url(../../assets/images/3/dialog_login_warm_login_btn_normal.png);
		background-size: 260/75rem 90/75rem;
		color: rgb(165, 75, 232);
	}
	
	.jdFlower_box_Logbtn_hint {
		color: rgb(169, 162, 180);
		font-size: 30/75rem;
		display: inline-block;
		margin-top: 60/75rem;
		margin-bottom: 30/75rem;
	}
	
	.jdFlower_box_Login_signbox {
		width: 560/75rem;
		/*border: 1px solid saddlebrown;*/
		margin-left: 20/75rem;
	}
	
	.jdFlower_box_Login_wx,
	.jdFlower_box_Login_qq,
	.jdFlower_box_Login_wb {
		float: left;
		height: 100/75rem;
		width: 100/75rem;
		margin: 0 43/75rem;
	}
	
	.jdFlower_box_Login_wx {
		background-image: url(../../assets/images/3/a微信-1.png);
		background-size: 100/75rem 100/75rem;
	}
	
	.jdFlower_box_Login_qq {
		background-image: url(../../assets/images/3/a朋友圈-1.png);
		background-size: 100/75rem 100/75rem;
	}
	
	.jdFlower_box_Login_wb {
		background-image: url(../../assets/images/3/a微博-1.png);
		background-size: 100/75rem 100/75rem;
	}
	
	.jdFlower_box_left_siginState {
		display: block;
	}
	/*登陆样式结束*/
</style>


