<template>
	<div class="starHeader">
		<router-link :to="{path:'/'+getTitle.paths}">
			<img src="../assets/images/47/f-1.png" alt="" />
		</router-link>
		<span>{{getTitle.names}}</span>
	</div>
</template>

<script>
	export default {
		
		name:"mineHead",
		data(){
			return {
				
			}
		},
		props:{
			getTitle:{
				type:Object,
				default:function(){
					return {names:'明星认证'}
				}
			}
		}
	}
</script>

<style lang="less">
	.starHeader{
		height:90/64rem;
		line-height: 90/64rem;
		text-align: center;
		background: #a54be8;
		position: relative;
		img{
			position:absolute;
			left:40/64rem;				
			width:18/64rem;
			height:32/64rem;
			top:( 90/64-32/64)/2rem;			
		}
		span{				
			
			font-size: 32/64rem;
			color:#ffffff;				
		}
	}
</style>


