<template>
	<div class="mineSetBtn">
		
		<MineHead :getTitle="title"/>

		<div class="newMessage">
			<p>新消息提醒</p>
			
			<div @click="addClick" :class="{'checked':isDorp}" class="switch">
				
			</div>
		</div>

		<div class="changeLi">
			<ul>				
				<router-link v-for="(data,index) in changeli" tag="li" :to="{path:'/mine/mineSetBtn/'+data.id}" :key="index">
					{{data.title}}<span></span>
				</router-link>
			</ul>
		</div>
		<router-link :to="{path:'/'}">
			<div class="out">退出登录</div>
		</router-link>
	</div>
</template>

<script>
	import MineHead from '../../../comments/mineHead';
	export default {
		name: 'mineSetBtn',
		data() {
			return {
				title:{
					names:'设置',
					paths:'mine'
					
				},
				isDorp:false,
				value1: true,
				changeli:[
					{
						title:'修改密码',
						id:'mineChangePassword'
					},
					{
						title:'修改绑定',
						id:'mineChangeBinding'
					}
				]
				
			}
		},
		components:{
			MineHead
		},
		methods:{
			addClick(event){
				this.isDorp = !this.isDorp;
			}
		}
	}
</script>

<style lang="less">
	.mineSetBtn {
		width: 10rem;
		
		.newMessage {
			position: relative;
			height: 90/64rem;
			line-height: 90/64rem;
			background: #ffffff;
			margin-top: 30/64rem;
			border-bottom: 1/64rem solid #d7d2e0;
			border-top: 1/64rem solid #d7d2e0;
			p {
				font-size: 36/64rem;
				color: #1B1B1B;
				padding-left: 30/64rem;
			}
			.switch {
				position: absolute;
				top: (90-50)/2/64rem;
				right: 30/64rem;
				width:100/64rem;
				height:50/64rem;
				background:#ccc;
				border-radius:50/64rem;
				
			}
			.switch:before{
				position: absolute;
				top:4/64rem;
				left:4/64rem;
				content:'';
				width:42/64rem;
				height: 42/64rem;
				border-radius:50%;
				background:white;
				transition: 0.3s ease-in;
			}
			.switch.checked{
				background: #a54be8;
			}
			.switch.checked:before{
			
				left:calc(100% - 46px);
	
			}
		}
		.changeLi {
			ul {
				padding-left: 30/64rem;
				padding-right: 20/64rem;
				margin-top: 40/64rem;
				background: #ffffff;
				font-size: 36/64rem;
				color: #1B1B1B;
				border-bottom: 1/64rem solid #d7d2e0;
				border-top: 1/64rem solid #d7d2e0;
				li {
					line-height: 90/64rem;
					border-bottom: 1/64rem solid #d7d2e0;
					font-family: "微软雅黑";
					span {
						display: block;
						width: 18/64rem;
						height: 30/64rem;
						float: right;
						margin-top: 32/64rem;
						margin-right: 32/64rem;
						background: url('../../../assets/images/47/插入箭头.png') no-repeat center center;
					}
				}
				li:last-child {
					border: none
				}
			}
		}
		.out {
			margin-top: 450/64rem;
			height: 90/64rem;
			line-height: 90/64rem;
			background: #ffffff;
			text-align: center;
			font-size: 36/64rem;
			color: #e43c3c;
			border-bottom: 1/64rem solid #d7d2e0;
			border-top: 1/64rem solid #d7d2e0;
		}
	}
	
	.el-switch.is-checked .el-switch__core {
		border-color: #a54be8;
		background-color: #a54be8;
	}
</style>


